.custom-navbar .nav-color {
    background-color: #ffffff;
}
 
.custom-navbar .nav-item a {
    color: #000000;
    text-transform: uppercase;
    font-weight: 500;
}

.custom-navbar .nav-item :hover{
    color: #B1D8D1;
    transition: all .4s ease;
    -webkit-transition: all .4s ease;
}